import React, { Component } from 'react'
import ReactPlayer from 'react-player'

import activityTypes from '..'

class InteractiveVideo extends Component {
  ref = player => {
    this.player = player
  }

  state = {
    questionIndex: -1,
    solutions: [],
    playing: true
  }

  componentDidMount() {
    const { activity } = this.props
    const {
      extra: { questions }
    } = activity

    setTimeout(() => {
      this.player.seekTo(0)
      // this.player.stop()
    }, 7000)
    const arr = []
    arr.length = questions.length
    this.setState({ solutions: arr })
  }

  handleProgress = ({ playedSeconds }) => {
    // console.log('onProgress', state)
    const { activity } = this.props

    const { solutions } = this.state

    const {
      extra: { questions }
    } = activity
    const questionIndex = questions.findIndex(
      ({ time }, index) => !solutions[index] && playedSeconds > time && playedSeconds < time + 5
    )

    if (questionIndex >= 0) {
      this.setState({ questionIndex, playing: false })
    }
  }

  finishQuestion = questionIndex => {
    this.setState(prevState => {
      prevState.solutions[questionIndex] = true
      return {
        questionIndex: -1,
        playing: true,
        solutions: prevState.solutions
      }
    })
  }

  renderQuestion(question, questionIndex) {
    const { playing } = this.state
    if (questionIndex >= 0) {
      const styles = {
        top: question.top,
        right: question.left,
        width: question.width,
        height: question.height,
        backgroundColor: 'green',
        position: 'absolute'
      }
      const Activity = activityTypes[question.activity.type]
      return (
        <div style={styles}>
          <Activity activity={question.activity} />
          <button onClick={() => this.finishQuestion(questionIndex)}>finish</button>
        </div>
      )
    }
  }

  render() {
    const { activity } = this.props
    const {
      extra: { questions }
    } = activity
    const { playing, questionIndex } = this.state

    return (
      <div style={{ position: 'relative' }}>
        -- {questionIndex} -- <br />
        {this.renderQuestion(questions[questionIndex], questionIndex)}
        <ReactPlayer
          ref={this.ref}
          url={activity.extra.url}
          controls={true}
          playing={playing}
          volume={0}
          onProgress={this.handleProgress}
        />
      </div>
    )
  }
}

export default InteractiveVideo
