import activityTypes from '../components/activities/activity'

function adjustActivities(level, course) {
  return level.activities.map(activity => {
    if (activity.type === activityTypes.types.Template) {
      activity = {
        ...activity,
        ...course.defaultQuestions.find(dq => dq.id === activity.templateId),
        id: activity.id
      }
    }
    if (activity.type === activityTypes.types.InteractiveVideo) {
      activity.extra.questions.forEach(question => {
        question.activity = adjustActivity(question.activity, course)
      })
    }
    return activity
  })
}

function adjustActivity(activity, course) {
  if (activity.type === activityTypes.types.Template) {
    activity = {
      ...activity,
      ...course.defaultQuestions.find(dq => dq.id === activity.templateId),
      id: activity.id
    }
  }
  return activity
}

export { adjustActivities, adjustActivity }
