import React from 'react'
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import styles from './style'
import { makeStyles, useTheme } from '@material-ui/core/styles'

const useStyles = makeStyles(styles)

function shuffle(array) {
  var currentIndex = array.length,
    temporaryValue,
    randomIndex

  while (currentIndex !== 0) {
    randomIndex = Math.floor(Math.random() * currentIndex)
    currentIndex -= 1
    temporaryValue = array[currentIndex]
    array[currentIndex] = array[randomIndex]
    array[randomIndex] = temporaryValue
  }
  array.forEach((element, index) => {
    element.index = index
  })
  return array
}

function prepareCard(str) {
  let arr = str.split(',').map((char, index) => ({
    char: char,
    match: false,
    show: false
  }))
  arr = arr.flatMap(i => [{ ...i }, { ...i }])
  return shuffle(arr)
}

export default ({ activity }) => {
  const classes = useStyles()
  const [cards, setCards] = React.useState(prepareCard(activity.extra))

  const onCardClick = card => {
    if (card.match) {
      return
    }
    const preClick = cards.find(c => !!c.show && !c.match)
    if (!card.show && preClick) {
      if (preClick.char === card.char) {
        preClick.match = true
        cards[preClick.index] = preClick

        card.show = true
        card.match = true
        cards[card.index] = card
        setCards([...cards])
      } else {
        card.show = true
        cards[card.index] = card
        setCards([...cards])
        setTimeout(() => {
          preClick.show = false
          cards[preClick.index] = preClick

          card.show = false
          cards[card.index] = card
          setCards([...cards])
        }, 1000)
      }
    } else {
      card.show = !card.show
      cards[card.index] = card
      setCards([...cards])
    }
  }

  return (
    <div className={classes.root}>
      {cards.map(card => (
        <Paper className={classes.card} onClick={() => onCardClick(card)}>
          {card.show || card.match ? card.char : '?'}
        </Paper>
      ))}
    </div>
  )
}
